<template>
  <div class="home container">
    <div class="row">
      <div class="col py-5">
        <!--分頁功能-->
        <p>高雄YouBike2.0公共自行車租賃站即時資訊</p>
        <!-- <button @click="mapshow = !mapshow" @click.once="showMap" class="mb-2">地圖查看</button> -->
        <!-- 將地圖標籤設置圖樣 選擇區域後地圖更動 -->
        <!-- 初始化地圖設定 -->
        <div id="map" style="height: 300px"></div>
        <div class="mt-3">
          <select v-model="areaSelected" class="mr-3">
            <option disabled value="">請選擇鄰近區域</option>
            <option v-for="(option,index) in areaOptions" :key="index">
              {{ option }}
            </option>
          </select>
          <select v-model="sortselected">
            <option disabled value="">排序方式(多到少)</option>
            <option v-for="(sort,index) in sortOptions" :value="sort.value" :key="index">
              {{ sort.txt }}
            </option>
          </select>
          <table class="table mt-3">
            <thead class="thead-light">
              <tr class="align-middle">
                <th class="align-middle text-nowrap" scope="col">租賃站名稱</th>
                <th class="align-middle text-nowrap">行政區</th>
                <th class="align-middle text-nowrap">車輛資訊</th>
                <th class="align-middle text-nowrap">地址</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, index) in initData" :key="index" v-show="areaSelected === '' || areaSelected === item.sarea">
                <th class="align-middle" scope="row">{{ namefilter(item.sna) }}</th>
                <th class="align-middle">{{ item.sarea }}</th>
                <th class="align-middle text-nowrap text-left">可借車輛: {{ item.sbi }}<br>可還車輛: {{ item.bemp }}<br>總車位: {{ item.tot }}</th>
                <th class="align-middle">
                  <a :href="`https://www.google.com/maps/dir//${item.lat},${item.lng}`" target="_blank" style="color: blue">{{item.ar}}</a>
                </th>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
    <!-- <img alt="Vue logo" src="../assets/logo.png"> -->
    <!-- <HelloWorld msg="Welcome to Your Vue.js App"/> -->
  </div>
</template>

<script>
import axios from 'axios'
import L from 'leaflet'

let osmMap = {}
//  openStreetMap 空物件主要會有兩個用途：
// 設定畫面中 #map 的基本設定，例如顯示的中心位置、縮放比例等。
// 將之後疊加在地圖上的操作指定加在 openStreetMap 之上。
let layer
let myIcon = L.icon({
  iconUrl: 'https://pngimg.com/uploads/gps/gps_PNG4.png',
  iconSize: [12, 18]
})
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
export default {
  name: 'Ubike',
  components: {
    // HelloWorld
  },
  data () {
    return {
      mapshow: true,
      areaOptions: [],
      areaSelected: '',
      sortselected: '',
      initData: [],
      varietytrans: [
        { sna: '租賃站場站名稱' },
        { sarea: '行政區' },
        { ar: '地址' },
        { tot: '總車位' },
        { sbi: '可借車輛' }
      ],
      sortOptions: [
        { value: 'tot', txt: '總車位' },
        { value: 'sbi', txt: '可借車輛' },
        { value: 'bemp', txt: '可還車輛' }
      ]
    }
  },
  watch: {
    sortselected () {
      this.changeSort(this.sortselected)
    },
    areaSelected () {
      const layers = []
      // clear markers
      osmMap.eachLayer((layer) => {
        if (layer instanceof L.Marker) {
          osmMap.removeLayer(layer)
        }
      })
      this.initData.forEach(item => {
        if (this.areaSelected === item.sarea) {
          layer = L.marker([item.lat, item.lng], { icon: myIcon }).bindPopup(`<b>${this.namefilter(item.sna)}<b><br>可借車輛: ${item.sbi}<br>可還車輛: ${item.bemp}<br>地址: <a href="https://www.google.com/maps/dir//${item.lat},${item.lng}" style="color: blue">${item.ar}</a>`)
          layers.push(layer)
        }
      })
      const myGroup = L.layerGroup(layers)
      osmMap.addLayer(myGroup)
    }
  },
  mounted () {
    // this.getRate()
    console.log('navigator', navigator.geolocation.getCurrentPosition)
    osmMap = L.map('map', {
      center: [22.68, 120.38],
      zoom: 10
    })
    // // let myIcon = L.divIcon({ className: 'my-div-icon' })
    myIcon = L.icon({
      iconUrl: 'https://pngimg.com/uploads/gps/gps_PNG4.png',
      iconSize: [12, 18]
    })
    // // you can set .my-div-icon styles in CSS
    L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png?{foo}', { foo: 'bar', attribution: 'Map data &copy; <a href="https://www.openstreetmap.org/">OpenStreetMap</a> contributors, <a href="https://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>' }).addTo(osmMap)
    // layer = L.marker([22.58, 120.38], { icon: myIcon }).addTo(osmMap)
    axios({
      method: 'get',
      url: 'https://api.kcg.gov.tw/api/service/Get/b4dd9c40-9027-4125-8666-06bef1756092',
      responseType: 'json'
    }).then(res => {
      this.initData = res.data.data.retVal
      for (let i = 0; i < res.data.data.retVal.length; i++) {
        this.areaOptions.push(res.data.data.retVal[i].sarea)
      }
      this.areaOptions = [...new Set(this.areaOptions)]
      for (let j = 0; j < this.initData.length; j++) {
        layer = L.marker([this.initData[j].lat, this.initData[j].lng], { icon: myIcon }).addTo(osmMap)
      }
    })
  },
  methods: {
    // getRate () {
    //   axios({
    //     method: 'get',
    //     url: 'https://api.kcg.gov.tw/api/service/Get/b4dd9c40-9027-4125-8666-06bef1756092',
    //     responseType: 'json'
    //   }).then(res => {
    //     this.initData = res.data.data.retVal
    //     console.log(this.initData)
    //     for (let i = 0; i < res.data.data.retVal.length; i++) {
    //       this.areaOptions.push(res.data.data.retVal[i].sarea)
    //     }
    //     this.areaOptions = [...new Set(this.areaOptions)]
    //   })
    // },
    changeSort (value) {
      this.initData = this.initData.sort((a, b) => {
        switch (value) {
          case 'tot':
            return b.tot - a.tot
          case 'sbi':
            return b.sbi - a.sbi
          case 'bemp':
            return b.bemp - a.bemp
          default:
            break
        }
      })
    },
    namefilter (value) {
      if (!value) return ''
      value = value.split('_')
      return value[1]
    }
  }
}
</script>
